<template>
  <div class="">

    <div class="bloc">
      <div class="">
        <nav class="red accent-2">
          <div class="nav-wrapper">
            <div class="col s12 center-align red accent-2">
              <a href="">{{ data_chat.users.username }} </a>
            </div>
          </div>
        </nav>
        <div class="nav_action" v-if="data_chat.users.username">
          <router-link :to="{ name: 'Profile', params: { username:  data_chat.users.username }}" class="btn waves-effect waves-light blue-grey darken-3">profile <i class="material-icons left">person_pin</i></router-link>
        </div>


        <div class="col s12 bloc_message" id="test">

          <div class="message_loading" v-if="data_chat.messages.length === undefined">
            <div class="preloader-wrapper big active">
              <div class="spinner-layer spinner-red-only">
                <div class="circle-clipper left"><div class="circle"></div></div><div class="gap-patch"><div class="circle"></div></div>
                <div class="circle-clipper right"><div class="circle"></div></div>
              </div>
            </div>
          </div>

            <ul class="messages">
              <li v-for="item in data_chat.messages">
                <div class="image" v-if="item.class !== 'moderation'" :style="{ 'background-image': 'url(' + (image_message(item.uid_receiver)) + ')' }" :class="{ 'right': data_chat.users.uid === item.uid_receiver }"></div>
                <div class="message" :class="{ 'right': data_chat.users.uid === item.uid_receiver, 'moderation': item.class === 'moderation' }"> {{ item.message }} </div>
                <small :class="{ 'right_small': data_chat.users.uid === item.uid_receiver }">{{ item.timestamp | formatdate }} <i style="font-size: 13px; display:none;" class="material-icons red-text accent-2-text">done</i></small>
              </li>
            </ul>

          <div class="grey lighten-3 bottom_box">

            <form class="col s12">
              <div class="row center-align" v-show="!connect">
                Connexion impossible
              </div>
              <div class="row" v-show="connect">
                <div class="input-field" style="width: CALC(100% - 145px); margin-left: 20px; position: relative; float: left;">
                    <textarea placeholder="Placeholder" id="first_name" type="text" class="materialize-textarea active" style="max-height:80px;height: 80px" v-model="newmessage" v-on:keyup="keymonitor"></textarea>
                  <label for="first_name" class="active">Message {{ connect }}</label>
                </div>
                <div class="input-field" style="width: 100px; position: relative;float: right;">
                  <button style="height: 80px;" class="waves-effect waves-light btn red accent-2"><i class="material-icons">send</i></button>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>

    <div id="modal1" class="modal open open_modal bg-transparent" v-show="loading_modal">
      <div class="modal-content center-align">
        <div class="preloader-wrapper big active">
          <div class="spinner-layer spinner-blue-only">
            <div class="circle-clipper left">
              <div class="circle"></div>
            </div><div class="gap-patch">
            <div class="circle"></div>
          </div><div class="circle-clipper right">
            <div class="circle"></div>
          </div>
          </div>
        </div>
      </div>
    </div>



    <div v-show="loading_modal" class="modal-overlay"></div>
  </div>


  </div>
</template>

<script type="text/babel">
  import Socket from './Socket.js'
  import Vuex from 'vuex'
  import axios from 'axios'

  export default {
    store: Socket,
    name: 'messageriechat',
    data () {
      return {
        id: 0,
        users: {},
        newmessage: ''
      }
    },

    computed: {
      ...Vuex.mapGetters(['clientId', 'connect', 'userdata', 'list_friends', 'loading_friends', 'loading_modal', 'data_chat', 'api_url'])
    },

    methods: {
      ...Vuex.mapActions(['auth_users', 'logout']),
      keymonitor: function (event) {
        this.$socket.emit('messagerie_viewed', {'uid_sender': this.userdata.uid, 'uid_receiver': this.data_chat.users.uid})
        if (event.key === 'Enter' && !event.shiftKey) {
          this.send_message()
        }
      },
      chat_load: function () {
        axios.get(this.api_url + '/api/messageries/chat/' + this.$route.params.id, { headers: { 'Authorization': localStorage.getItem('token') } }).then(reponse => {
          if (reponse.data.status) {
            Socket.state.data_chat = reponse.data
          } else {
            this.$toast.error({message: reponse.data.message, position: 'top right'})
            this.$router.push('/')
          }
        })
      },

      profile_loaded: function () {
        if (this.userdata.title === null || this.userdata.bio === null) {
          this.$router.push('/login')
        }
      },

      send_message: function () {
        if (this.newmessage.trim() !== '') {
          var data = {'uid_sender': this.userdata.uid, 'uid_receiver': this.data_chat.users.uid, 'message': this.newmessage, 'timestamp': new Date() / 1000 | 0}
          this.list_friends[this.data_chat.users.uid]['message'] = this.newmessage.trim()
          this.$socket.emit('messagerie_newmessage', data)
          this.newmessage = ''
        } else {
          this.$toast.error({message: 'Please enter a valid message', position: 'top right'})
        }
      },
      image_message: function (uid) {
        if (this.data_chat.users.uid !== uid) {
          return this.data_chat.users.imageBase64
        }
        return this.userdata.picture
      }
    },

    beforeUpdate: function () {
      console.log('load' + this.$route.params.id)
    },

    updated () {
      if (Socket.state.data_chat_id !== this.$route.params.id && this.$route.name === 'messageriechat') {
        Socket.state.data_chat_id = this.$route.params.id
        Socket.state.data_chat = { users: {}, messages: {} }
        this.chat_load()
      }
      if (this.$route.name === 'messageriechat') {
        var someElement = document.getElementById('test')
        someElement.scrollTop = someElement.scrollHeight
      }
    },

    created () {
      this.auth_users()
      Socket.state.data_chat_id = parseInt(this.$route.params.id)
      this.chat_load()
    }
  }
</script>




