<template>
  <div class="">

    <div class="bloc">
      <div class="row">
        <nav class="no-shadow">
          <div class="nav-wrapper">
            <div class="col s12 center-align red accent-2">
              <a href="">Notifications</a>
            </div>
          </div>
        </nav>

        <div class="col s12">
          <ul class="collection">
            <div v-for="item in notifications.data">
              <router-link v-if="item.uid_interested" class="collection-item avatar" :to="{ name: 'Profile', params: { username: item.uid_interested }}">
                <img :src="(api_url + '/api/users/uid/photo/' + item.uid_interested)" alt="" class="circle">
                <span class="title">{{ item.title }} <span class="blue-grey-text text-darken-3" style="font-size:13px; font-style: italic">{{ item.timestamp | formatdate }}</span></span><span class="new badge" v-show="!item.viewed"></span></span>
                <p class="grey-text text-darken-2">{{ item.message | truncate(50, '...') }}</p>
              </router-link>
            </div>
            <div v-if="loading_friends === true" class="center-align">
              <div class="preloader-wrapper small active">
                <div class="spinner-layer spinner-red-only">
                  <div class="circle-clipper left"><div class="circle"></div></div><div class="gap-patch"><div class="circle"></div></div><div class="circle-clipper right"><div class="circle"></div></div>
                </div>
              </div>
            </div>
          </ul>
        </div>
      </div>
    </div>
    <div id="modal1" class="modal open open_modal bg-transparent" v-show="loading_modal">
      <div class="modal-content center-align">
        <div class="preloader-wrapper big active">
          <div class="spinner-layer spinner-blue-only">
            <div class="circle-clipper left">
              <div class="circle"></div>
            </div><div class="gap-patch">
            <div class="circle"></div>
          </div><div class="circle-clipper right">
            <div class="circle"></div>
          </div>
          </div>
        </div>
      </div>
    </div>



    <div v-show="loading_modal" class="modal-overlay"></div>
  </div>


</template>

<script type="text/babel">
  import config from './Config.vue'
  import Socket from './Socket.js'
  import Vuex from 'vuex'
  import axios from 'axios'

  export default {
    store: Socket,
    name: 'Notifications',
    data () {
      return {
        users: {},
        finder: {},
        socket_state: {}
      }
    },

    computed: {
      ...Vuex.mapGetters(['clientId', 'connect', 'userdata', 'list_friends', 'loading_friends', 'loading_modal', 'notifications', 'api_url'])
    },

    methods: {
      ...Vuex.mapActions(['auth_users', 'logout']),

      profile_loaded: function () {
        if (this.userdata.title === null || this.userdata.bio === null) {
          this.$router.push('/')
        }
      }
    },

    created () {
      config.check_session().then(response => {
        if (response.data.status === true) {
          this.users = response.data.data
          this.profile_loaded()
          this.notifications.new = false
          axios.get(this.api_url + '/api/users/viewed/notifications', {headers: {'Authorization': localStorage.getItem('token')}}).then(response => {
            if (this.notifications.data) {
              for (var i = 0; this.notifications.data[i]; i++) {
                this.notifications.data[i].viewed = true
              }
            }
          })
        } else {
          this.$router.push('/login')
        }
      }).catch(response => {
        this.$router.push('/error')
      })
    }
  }
</script>


<style media="screen">

</style>



