<template>
  <div>
    <div class="background-image"></div>
    <div class="background-over"></div>
    <div class="container" style="margin-top: 100px">
      <div class="row">
        <div class="col s12 m12">
          <div class="card">
            <div class="card-content">
              <span class="card-title">Reset my password</span>
              <div style="text-align: center; display: none;" ref="loader">
                <i class="fa fa-circle-o-notch fa-spin fa-3x fa-fw"></i>
              </div>
              <form v-on:submit.prevent="reset" ref="form">
                <div class="row">
                  <div class="input-field inline col s12">
                    <input id="password" v-model="password" type="password">
                    <label for="password" class="active">New password</label>
                    <p class="medium-small grey-text" style="font-style: italic;">Password
                      restriction: at least one <span class="red-text text-accent-2">upper</span>
                      and <span class="red-text text-accent-2">lowercase</span>, one <span
                        class="red-text text-accent-2">digit</span> and one <span
                        class="red-text text-accent-2">special character</span> (?!@..). And
                      at least 6 characters</p>
                  </div>

                  <div class="input-field inline col s12">
                    <input id="newpassword" v-model="newpassword" type="password">
                    <label for="newpassword" class="active"> To be on the new password</label>
                  </div>
                </div>
                <div class="right-align">
                  <button class="btn waves-effect waves-light red accent-2" type="submit" name="action">Reset
                    <i class="material-icons right">done</i>
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>


<script type="text/babel">

  import config from './Config.vue'

  export default {
    name: 'Reset',
    data () {
      return {
        newpassword: '',
        password: ''
      }
    },

    methods: {
      buttonAnimation: function (status) {
        if (!status) {
          this.$refs.loader.style.display = 'block'
          this.$refs.form.style.display = 'none'
        } else {
          this.$refs.loader.style.display = 'none'
          this.$refs.form.style.display = 'inherit'
        }
      },

      reset: function (e) {
        e.preventDefault()
        this.buttonAnimation(false)
        if (this.password === '' || this.newpassword === '') {
          this.$toast.error({ message: 'All fields required' })
          this.buttonAnimation(true)
        } else {
          this.$http.put(config.url_api + '/api/reset/' + this.$route.params.token, { 'password': this.password, 'newpassword': this.newpassword }).then(response => {
            response.json().then((data) => {
              if (data.status === true) {
                this.$toast.info({message: data.message})
                this.$router.push('/login')
              } else {
                this.$toast.error({message: data.message})
              }
              this.buttonAnimation(true)
            })
          }, response => {
            this.$router.push('/error')
            this.$toast.error({message: 'Connection error with server'})
          })
        }
      }
    },

    beforeCreate () {
      config.check_session().then(response => {
        if (response.data.status === true) {
          this.$router.push('/')
        }
      }).catch(response => {
        this.$router.push('/error')
        this.$toast.error({message: 'Connection error with server'})
      })
    }
  }

</script>

<style media="screen">
  body
  {
    animation: cdhange-color 8s infinite;
  }
</style>



