<template>
    <div>
        <div class="background-image"></div>
        <div class="background-over"></div>
        <div class="container" style="margin-top: 100px">
            <div class="row">
                <div class="col s12 m12">
                    <div class="card">
                        <ul class="tabs blue darken-3">
                            <li class="tab col s6"><router-link to="login" class="active">Sign In</router-link></li>
                            <li class="tab col s6"><router-link to="register">Sign Up</router-link></li>
                        </ul>
                        <div class="card-content">
                            <form v-on:submit.prevent="login">
                                <div class="row">
                                    <div class="input-field inline col s12">
                                        <input id="username" v-model="username" type="text">
                                        <label for="username" class="active">Username</label>
                                    </div>
                                    <div class="input-field inline col s12">
                                        <input id="password" v-model="password" name="password" type="password">
                                        <label for="password" class="active">Password</label>
                                    </div>
                                </div>
                                <div class="right-align">

                                <button class="btn waves-effect waves-light red accent-2" type="submit" name="action">Submit
                                    <i class="material-icons right">send</i>
                                </button>
                                <router-link to="/forgot" class="btn waves-effect waves-light blue-grey">Forgot my password ?</router-link>

                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script type="text/babel">

  import config from './Config.vue'
  import Socket from './Socket.js'
  import Vuex from 'vuex'

  export default {
    store: Socket,
    name: 'Login',
    data () {
      return {
        username: '',
        password: ''
      }
    },

    methods: {
      ...Vuex.mapActions(['socket_auth']),
      login: function (e) {
        e.preventDefault()
        if (this.username === '' || this.password === '') {
          this.$toast.error({ message: 'All fields required' })
        } else {
          this.$http.post(config.url_api + '/api/login', {
            'username': this.username,
            'password': this.password
          }).then(response => {
            response.json().then((data) => {
              if (data.status) {
                if (data.token_session) {
                  this.$toast.info({message: data.message})
                  localStorage.setItem('token', data.token_session)
                  this.$socket.connect()
                  if (data.action === 'otp') {
                    this.$router.push('/otp')
                  } else {
                    this.$router.push('/')
                  }
                } else {
                  this.$toast.error({message: 'An error as occured'})
                }
              } else {
                this.$toast.error({message: data.message})
              }
            })
          }, response => {
            this.$router.push('/error')
            this.$toast.error({message: 'Connection error with server'})
          })
        }
      }
    },

    beforeCreate () {
      config.check_session().then(response => {
        if (response.data.status === true) {
          this.$router.push('/')
        }
      }).catch(response => {
        this.$router.push('/error')
        this.$toast.error({message: 'Connection error with server'})
      })
    }
  }

</script>

<style media="screen">
  body
  {
    animation: cdhange-color 8s infinite;
  }
</style>



