<template>
    <div>
        <div class="background-image"></div>
        <div class="background-over"></div>
        <div class="container" style="margin-top: 100px">
            <div class="row">
                <div class="col s12 m12">
                    <div class="card">
                        <ul class="tabs blue darken-3">
                            <li class="tab col s6"><router-link to="login">Sign In</router-link></li>
                            <li class="tab col s6"><router-link to="register" class="active">Sign Up</router-link></li>
                        </ul>
                        <div class="card-content">
                            <form v-on:submit.prevent="login">
                                <div class="row">
                                    <div class="input-field inline col s12">
                                        <input id="email" v-model="register.email" :class="{ 'invalid': !validateEmail(register.email) && register.email !== '' }" type="text">
                                        <label for="email" class="active">Email</label>
                                        <p v-if="!validateEmail(register.email) && register.email !== ''"><i class="red-text">Invalid email</i></p>
                                    </div>
                                    <div class="input-field inline col s12">
                                        <input id="username" v-model="register.username" :class="{ 'invalid': !validateLenght(register.username, 3) && register.username !== '' }" type="text">
                                        <label for="username" class="active">Username</label>
                                        <p v-if="!validateLenght(register.username, 3) && register.username !== ''"><i class="red-text">The username must be at least 4 characters</i></p>
                                    </div>
                                    <div class="input-field inline col s12">
                                        <input id="firstname" v-model="register.firstname" :class="{ 'invalid': !validateLenght(register.firstname, 2) && register.firstname !== '' }" type="text">
                                        <label for="firstname" class="active">FirstName</label>
                                        <p v-if="!validateLenght(register.firstname, 2) && register.firstname !== ''"><i class="red-text">Invalid Firstname</i></p>
                                    </div>
                                    <div class="input-field inline col s12">
                                        <input id="lastname" v-model="register.lastname" :class="{ 'invalid': !validateLenght(register.lastname, 2) && register.lastname !== '' }" type="text">
                                        <label for="lastname" class="active">LastName</label>
                                        <p v-if="!validateLenght(register.lastname, 2) && register.lastname !== ''"><i class="red-text">Invalid LastName</i></p>
                                    </div>
                                    <div class="input-field inline col s12">
                                        <input id="password" v-model="register.password" name="password" :class="{ 'invalid': !validatePassword(register.password) && register.password !== '' }" type="password">
                                        <label for="password" class="active">Password</label>
                                      <p class="medium-small grey-text" style="font-style: italic;" :class="{ 'red-text': !validatePassword(register.password) && register.password !== '' }">Password
                                        restriction: at least one <span class="red-text text-accent-2">upper</span>
                                        and <span class="red-text text-accent-2">lowercase</span>, one <span
                                          class="red-text text-accent-2">digit</span> and one <span
                                          class="red-text text-accent-2">special character</span> (?!@..). And
                                        at least 6 characters</p>
                                    </div>
                                </div>
                                <div class="right-align">
                                    <button class="btn waves-effect waves-light red accent-2" :class="{ 'disabled': !register.button }" type="submit" name="action">Submit
                                        <i class="material-icons right">send</i>
                                    </button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script type="text/babel">

  import config from './Config.vue'
  import VueRouter from 'vue-router'

  export default {
    name: 'Register',
    data () {
      return {
        register: {
          email: '',
          username: '',
          firstname: '',
          lastname: '',
          password: '',
          button: true
        }
      }
    },

    methods: {
      validateEmail: function (email) {
        var re = /\S+@\S+\.\S+/
        return re.test(email)
      },
      validateLenght: function (val, lenght) {
        if (val.replace(/ /g, '').length > lenght) {
          return true
        }
        return false
      },
      validatePassword: function (password) {
        return true
      },
      login: function (e) {
        e.preventDefault()
        if (this.register.button === true) {
          this.button = false
          if (!this.validateEmail(this.register.email) || !this.validateLenght(this.register.username, 3) || !this.validateLenght(this.register.firstname, 2) || !this.validateLenght(this.register.lastname, 2) || !this.validatePassword(this.register.password)) {
            this.$toast.error({message: 'Check your fields'})
            this.button = true
          } else {
            this.send_post()
          }
        }
      },
      send_post: function () {
        this.$http.post(config.url_api + '/api/register', this.register).then(response => {
          response.json().then((data) => {
            if (data.status) {
              this.$toast.info({message: data.message})
              var router = new VueRouter()
              router.push('/login')
            } else {
              this.$toast.error({message: data.message})
            }
          })
          this.button = true
        }, (response) => {
          this.$router.push('/error')
          this.$toast.error({message: 'Connection error with server'})
          this.button = true
        })
      }
    },

    beforeCreate () {
      config.check_session().then(response => {
        if (response.data.status === true) {
          this.$router.push('/')
        }
      }).catch(response => {
        this.$router.push('/error')
        this.$toast.error({message: 'Connection error with server'})
      })
    }
  }

</script>

<style media="screen">
    body
    {
        animation: cdhange-color 8s infinite;
    }


</style>



