<template>
  <div class="">

    <div class="bloc">
      <div class="message_loading" v-if="loader_finder === true">
        <div class="preloader-wrapper big active">
          <div class="spinner-layer spinner-red-only">
            <div class="circle-clipper left"><div class="circle"></div></div><div class="gap-patch"><div class="circle"></div></div>
            <div class="circle-clipper right"><div class="circle"></div></div>
          </div>
        </div>
      </div>
      <div class="row">
        <nav>
          <div class="nav-wrapper">
            <div class="col s12 center-align red accent-2">
              <a href="">Finder</a>
            </div>
          </div>
        </nav>
          <div class="card col s12" style="z-index: 2; margin:auto;box-shadow: none;">
            <transition name="custom-classes-transition"
                        enter-active-class="animated fadeIn">
            <div class="card-content  right-align" v-if="!filter.search">
              <button type="submit" v-on:click="filter.search = true" class="waves-effect waves-light btn blue-grey darken-4"><i class="material-icons right">settings_input_component</i> Filter the results</button>

              <div class="row sortMage">
                <h5 class="left-align">Order by</h5>
                <select class="browser-default sortPadd col s12 m6 l2" v-model="selectArray.birthday" v-on:change="sort($event, 'birthday')">
                  <option value="0" disabled selected>Age</option>
                  <optgroup label="">
                    <option value="desc">Descending</option>
                    <option value="asc">Ascending</option>
                  </optgroup>
                </select>

                <select class="browser-default col s12 m6 l2 sortPadd" v-model="selectArray.distance" v-on:change="sort($event, 'distance')">
                  <option value="0" disabled selected>Distance</option>
                  <optgroup label="">
                    <option value="asc">Near</option>
                    <option value="desc">Far</option>
                  </optgroup>
                </select>

                <select class="browser-default col s12 m6 l2 sortPadd" v-model="selectArray.connected" v-on:change="sort($event, 'connected')">
                  <option value="0" disabled selected>Is connected</option>
                  <optgroup label="">
                    <option value="desc">Yes</option>
                    <option value="asc">No</option>
                  </optgroup>
                </select>

                <select class="browser-default col s12 m6 l2 sortPadd" v-model="selectArray.popularity" v-on:change="sort($event, 'popularity')">
                  <option value="0" disabled selected>Popularity</option>
                  <optgroup label="">
                    <option value="desc">Most</option>
                    <option value="asc">Less</option>
                  </optgroup>
                </select>

                <select class="browser-default col s12 m6 l2 sortPadd"v-model="selectArray.tags"  v-on:change="sort($event, 'tags')">
                  <option value="0" selected>Tags</option>
                  <optgroup label="">
                    <option value="desc">Most</option>
                    <option value="asc">Less</option>
                  </optgroup>
                </select>
                <div class="col s12 m6 l1">
                  <button type="submit" v-on:click="resetSort()" class="waves-effect waves-light btn  red accent-2" style="margin-top: 10px;">Reset sort</button>
                </div>

              </div>

            </div>
            </transition>
            <transition name="custom-classes-transition"
                        enter-active-class="animated fadeIn" leave-active-class="animated fadeOut">
            <div class="card-content" v-if="filter.search">
              <span class="card-title">Filter</span>
              <form class="row" v-on:submit.prevent="post_finder">

                <div class="input-field col s12 m6" style="height: 65px;">
                  <div style="margin-bottom: 30px;">
                    <label style="font-size: 13px;">Localisation</label>
                  </div>
                  <select class="browser-default" v-model="filter.pos">
                    <option value="" disabled selected>Choose your option</option>
                    <optgroup label="Near">
                      <option value="0">Aroud me</option>
                      <option value="25">~ 25km</option>
                      <option value="50">~ 50 km</option>
                      <option value="75">~ 75 km</option>
                      <option value="100">~ 100km</option>
                    </optgroup>
                    <optgroup label="Far">
                      <option value="250">~ 250km</option>
                      <option value="500">~ 500km</option>
                      <option value="1000">~ 1000km</option>
                      <option value="+">unlimited</option>
                    </optgroup>
                  </select>
                </div>

                <div class="input-field col s12 m6" style="height: 100px;">
                  <div style="margin-bottom: 30px;">
                    <label style="font-size: 13px;">Popularity</label>
                  </div>
                  <input class="with-gap" name="popularity" type="radio" id="popularity-new" value="1" v-model="filter.popularity"/>
                  <label for="popularity-new" style="margin-right: 30px;"><i class="material-icons red-text text-darken-1">sentiment_very_dissatisfied</i></label>

                  <input class="with-gap" name="popularity" type="radio" id="popularity-medium" value="2" v-model="filter.popularity"/>
                  <label for="popularity-medium" style="margin-right: 30px;"><i class="material-icons orange-text">sentiment_neutral</i></label>

                  <input class="with-gap" name="popularity" type="radio" id="popularity-popular" value="3" v-model="filter.popularity"/>
                  <label for="popularity-popular" style="margin-right: 30px;"><i class="material-icons blue-text">sentiment_satisfied</i></label>

                  <input class="with-gap" name="popularity" type="radio" id="popularity-very" value="4" v-model="filter.popularity"/>
                  <label for="popularity-very" style="margin-right: 30px;"><i class="material-icons green-text">sentiment_very_satisfied</i></label>

                  <input class="with-gap" name="popularity" type="radio" id="popularity-reset" value="0" v-model="filter.popularity" checked/>
                  <label for="popularity-reset" style="margin-right: 30px;"><i class="material-icons grey-text text-darken-3">clear</i></label>
                </div>

                <div class="input-field col s12 m6">
                  <div style="margin-bottom: 30px;">
                    <label style="font-size: 13px;">Tags search</label>
                  </div>
                  <multiselect
                    v-model="filter.tags"
                    :options="options"
                    :multiple="true"
                  >
                  </multiselect>
                </div>

                <div class="input-field col s12 m6">
                  <label class="active">Age</label>
                  <p style="margin-top: 60px;">
                    <vue-slider ref="slider" :min="16" :max="100" v-model="filter.age"></vue-slider>
                  </p>

                </div>

                <div class="input-field col s12 right-align">
                  <button type="submit" id="submit_filter"class="waves-effect waves-light btn red accent-2"><i class="material-icons right">search</i>search</button>
                </div>

              </form>
            </div>
          </transition>

          </div>
        <div class="bloc-parent">
        <div class="col s12 center-align" v-show="!sorted" style="padding-top: 50px;">
          <i class="material-icons large">sentiment_very_dissatisfied</i> <br/> No Results
        </div>
        <div v-for="item in sorted" class="col-user" :style="{ 'background-image': 'url(' + api_photo + '/' + item.piid + '.png)' }">
              <span class="card-title white-text">{{ item.username }}
                <i v-if="item.connected !== '0'" class="fa fa-circle green-text" aria-hidden="true"></i>
                <i v-if="item.sexe === '0'" class="fa fa-transgender light-green-text text-accent-4 pointer" v-my-tooltip.bottom-center="'Other sex'" aria-hidden="true"></i>
                <i v-if="item.sexe === '1'" class="fa fa-mars light-blue-text pointer" aria-hidden="true" v-my-tooltip.bottom-center="'Man'"></i>
                <i v-if="item.sexe === '2'" class="fa fa-venus pink-text lighten-1 pointer" aria-hidden="true" v-my-tooltip.bottom-center="'Woman'"></i>
              </span>
              <div v-if="item.distance">
                {{ item.distance | formatdistance }}
              </div>
          <router-link :to="{ name: 'Profile', params: { username: item.username }}" class="link">
            <i class="material-icons medium">pageview</i>
          </router-link>
        </div>
        </div>
      </div>
    </div>


    <div id="modal1" class="modal open open_modal" v-show="welcome_modal">
      <div class="modal-content">
        <h4>Welcome</h4>
        <p>You are new user, we need additional information</p>
        <form v-on:submit.prevent="new_profile">
          <div class="row">

            <div class="input-field inline col s12">
              <input id="title" v-model="users.title" type="text">
              <label id="bio" class="active">Profile title</label>
              <small><i>The profile title must be at least 6 characters long</i></small>
            </div>

            <div class="input-field inline col s12">
              <textarea id="bio" v-model="users.bio" class="materialize-textarea"></textarea>
              <label id="bio" class="active">Biography</label>
              <small><i>The profile title must be at least 6 words long</i></small>
            </div>

            <div class="input-field col s12" style="margin-top: 65px">
              <label for="" class="active">Birthdate</label>
              <datepicker v-model="users.birthday" :value="users.birthday"></datepicker>
            </div>

            <div class="input-field col s12">
              <div style="margin-bottom: 30px;">
                <label style="font-size: 13px;">Looking for</label>
              </div>
              <input class="with-gap" type="checkbox" id="Chat" value="Chat"
                     v-model="users.lookingfor"/>
              <label for="Chat" style="margin-right: 30px;">Chat</label>

              <input class="with-gap" type="checkbox" id="Dates" value="Dates"
                     v-model="users.lookingfor"/>
              <label for="Dates" style="margin-right: 30px;">Dates</label>

              <input class="with-gap" type="checkbox" id="Friends" value="Friends"
                     v-model="users.lookingfor"/>
              <label for="Friends" style="margin-right: 30px;">Friends</label>

              <input class="with-gap" type="checkbox" id="Networking" value="Networking"
                     v-model="users.lookingfor"/>
              <label for="Networking" style="margin-right: 30px;">Networking</label>

              <input class="with-gap" type="checkbox" id="Relationships" value="Relationships"
                     v-model="users.lookingfor"/>
              <label for="Relationships" style="margin-right: 30px;">Relationships</label>
            </div>


            <div class="input-field col s12" style="height: 70px">
                <div style="margin-bottom: 35px;">
                  <label>Your sex</label>
                </div>
                <input class="with-gap" name="sexe" type="radio" id="sexe-man" v-model="users.sexe" value="1"/>
                <label for="sexe-man">Man</label>

                <input class="with-gap" name="sexe" type="radio" id="sexe-woman" v-model="users.sexe" value="2"/>
                <label for="sexe-woman">Woman</label>

                <input class="with-gap" name="sexe" type="radio" id="sexe-other" v-model="users.sexe" value="0"/>
                <label for="sexe-other">Other</label>

            </div>

            <div class="input-field col s12" style="height: 70px">
              <div style="margin-bottom: 35px;">
                <label>Your orientation</label>
              </div>
              <input class="with-gap" name="orientation" type="radio" id="orientation-hetero" v-model="users.orientation" value="1"/>
              <label for="orientation-hetero">Heterosexual</label>

              <input class="with-gap" name="orientation" type="radio" id="orientation-homo" v-model="users.orientation" value="2"/>
              <label for="orientation-homo">Homosexual</label>

              <input class="with-gap" name="orientation" type="radio" id="orientation-bi" v-model="users.orientation" value="0"/>
              <label for="orientation-bi">Bisexual</label>

            </div>

          </div>
          <div class="right-align">
            <button class="btn waves-effect waves-light blue darken-3" type="submit" name="action">Saves
              <i class="material-icons right">send</i>
            </button>
          </div>
        </form>
      </div>
    </div>

    <div id="modal1" class="modal open open_modal bg-transparent" v-show="loading_modal">
      <div class="modal-content center-align">
        <div class="preloader-wrapper big active">
          <div class="spinner-layer spinner-blue-only">
            <div class="circle-clipper left">
              <div class="circle"></div>
            </div><div class="gap-patch">
            <div class="circle"></div>
          </div><div class="circle-clipper right">
            <div class="circle"></div>
          </div>
          </div>
        </div>
      </div>
    </div>



    <div v-show="welcome_modal || loading_modal" class="modal-overlay"></div>
    </div>


  </div>
</template>

<script type="text/babel">
  import config from './Config.vue'
  import Socket from './Socket.js'
  import Vuex from 'vuex'
  import axios from 'axios'
  import Multiselect from 'vue-multiselect'
  import vueSlider from 'vue-slider-component'
  import Datepicker from 'vuejs-datepicker'
  import _ from 'lodash'

  export default {
    store: Socket,
    name: 'Home',
    data () {
      return {
        users: {},
        finder: null,
        filter: {
          pos: 0,
          popularity: 0,
          age: [16, 100],
          tags: null,
          search: false
        },
        sorted: null,
        loader_finder: true,
        welcome_modal: false,
        options: ['list', 'of', 'options'],
        sortArray: [],
        reverseArray: [],
        selectArray: {
          birthday: 0,
          tags: 0,
          connected: 0,
          distance: 0,
          popularity: 0
        }
      }
    },

    components: { Multiselect, vueSlider, Datepicker },

    computed: {
      ...Vuex.mapGetters(['clientId', 'connect', 'userdata', 'list_friends', 'loading_friends', 'loading_modal', 'api_url', 'api_photo'])
    },

    methods: {
      ...Vuex.mapActions(['auth_users', 'logout']),
      new_profile: function (e) {
        e.preventDefault()
        this.$http.put(this.api_url + '/api/edit', this.users, {headers: {'Authorization': localStorage.getItem('token')}}).then(response => {
          response.json().then((data) => {
            if (data.status) {
              this.auth_users()
              this.get_finder()
              this.welcome_modal = false
              this.$toast.info({message: data.message})
            } else {
              this.$toast.error({message: data.message})
            }
          })
        })
      },

      resetSort: function () {
        this.sortArray = []
        this.reverseArray = []
        this.selectArray = {
          birthday: 0,
          tags: 0,
          connected: 0,
          distance: 0,
          popularity: 0
        }

        this.sorted = this.orderedUsers()
      },

      orderedUsers: function () {
        if (this.finder.length !== 0) {
          return _.orderBy(this.finder, this.sortArray, this.reverseArray)
        }
        return null
      },

      isExist: function (name) {
        for (var i = 0; i < this.sortArray.length; i++) {
          if (this.sortArray[i] === name) {
            return i
          }
        }
        return -1
      },

      sort: function (e, val) {
        if (this.isExist(val) === -1) {
          this.sortArray.push(val)
        }
        let key = this.isExist(val)
        if (key !== -1) {
          this.reverseArray[key] = e.target.value
        }
        this.sorted = this.orderedUsers()
      },

      post_finder: function (e) {
        e.preventDefault()
        this.buttonAnimation(document.getElementById('submit_filter'), false, '')
        axios.post(this.api_url + '/api/finder/search', this.filter, {headers: {'Authorization': localStorage.getItem('token')}}).then(response => {
          if (response.data.status) {
            this.finder = response.data.data
            this.resetSort()
          } else {
            this.$toast.error({message: response.data.message, position: 'top right'})
          }
          this.filter.search = false
          this.buttonAnimation(document.getElementById('submit_filter'), true, '<i class="material-icons right">search</i>search')
        })
      },

      get_finder: function () {
        if (this.$route.params.tags) {
          this.filter.tags = [this.$route.params.tags]
        }
        axios.post(this.api_url + '/api/finder/search', this.filter, {headers: {'Authorization': localStorage.getItem('token')}}).then(response => {
          if (response.data.status) {
            this.finder = response.data.data
            this.sorted = this.orderedUsers()
          }
          this.loader_finder = false
        })
      },

      get_tags: function () {
        axios.get(this.api_url + '/api/users/tags/list', {headers: {'Authorization': localStorage.getItem('token')}}).then(response => {
          if (response.data.status) {
            this.options = response.data.data
          }
        })
      },

      buttonAnimation: function (e, status, message) {
        if (!status) {
          e.innerHTML = '<i class="fa fa-circle-o-notch fa-spin fa-3x fa-fw"></i>'
          e.className = 'btn waves-effect waves-light red accent-2 disabled'
        } else {
          e.innerHTML = message
          e.className = 'btn waves-effect waves-light red accent-2'
        }
      },

      profile_loaded: function () {
        if (this.userdata.title === null || this.userdata.bio === null) {
          this.welcome_modal = true
        }
      }
    },

    filters: {
      capitalize: function (str) {
        return str.charAt(0).toUpperCase() + str.slice(1)
      }
    },

    created () {
      config.check_session().then(response => {
        if (response.data.status === true) {
          this.users = response.data.data
          this.profile_loaded()
        } else {
          this.$router.push('/login')
        }
      }).catch(response => {
        this.$router.push('/login')
      })

      this.get_tags()
      this.get_finder()
    }
  }
</script>

<style scoped>
  .sortMage {
    margin-top: 20px;
  }

  .sortPadd {
    margin: 10px 10px 0px 10px;
  }
</style>



