<script type="text/babel">
  import config from './Config.vue'
  import Socket from './Socket.js'
  import Vuex from 'vuex'
  import axios from 'axios'
  import Datepicker from 'vuejs-datepicker'
  import VueGoogleAutocomplete from 'vue-google-autocomplete'
  import Multiselect from 'vue-multiselect'

  let marked = require('marked')
  export default {
    store: Socket,
    'name': 'Settings',
    data () {
      return {
        dataprofile: {},
        pictures: {},
        users: {
          lookingfor: [],
          tags: []
        },
        addressData: null,
        security: { 'old': '', 'password': '', 'newpassword': '' },
        upload: {
          'comments': '',
          'data': false,
          'profile': 0
        },
        options: [],
        loading_button: true
      }
    },

    components: {
      Datepicker,
      VueGoogleAutocomplete,
      Multiselect
    },

    computed: {
      ...Vuex.mapGetters(['clientId', 'connect', 'userdata', 'list_friends', 'loading_friends', 'loading_modal', 'api_url']),

      compiledMarkdown: function () {
        return marked(this.users.bio || '')
      }
    },

    methods: {
      ...Vuex.mapActions(['auth_users', 'logout']),
      new_profile: function (e) {
        e.preventDefault()
        this.$http.put(this.api_url + '/api/edit', this.users, {headers: {'Authorization': localStorage.getItem('token')}}).then(response => {
          response.json().then((data) => {
            if (data.status) {
              this.auth_users()
              this.$toast.info({message: data.message, position: 'top right'})
            } else {
              this.$toast.error({message: data.message, position: 'top right'})
            }
          })
        })
      },

      updateBio: function (e) {
        this.users.bio = e.target.value
      },

      markdown: function (type) {
        if (type === 'image' || type === 'url') {
          var url = prompt('Please enter url', '')
          var name = prompt('Please enter name', '')
          var result = (type === 'image') ? '!' : ''

          if (url != null) {
            if (name != null) {
              this.users.bio = this.users.bio + '\n\n' + result + '[' + name + '](' + url + ')'
            } else {
              this.users.bio = this.users.bio + '\n\n' + result + '[' + url + '](' + url + ')'
            }
          }
        } else if (type === 'bold') {
          this.users.bio = this.users.bio + '\n\n**bold text**'
        } else if (type === 'italic') {
          this.users.bio = this.users.bio + '\n\n*italic text*'
        } else if (type === 'quote') {
          this.users.bio = this.users.bio + '\n\n>quote text'
        }
      },

      markdownSize: function (size) {
        if (size === 1) {
          this.users.bio = this.users.bio + '\n\n# size 1'
        } else if (size === 2) {
          this.users.bio = this.users.bio + '\n\n## size 2'
        } else if (size === 3) {
          this.users.bio = this.users.bio + '\n\n### size 3'
        } else if (size === 4) {
          this.users.bio = this.users.bio + '\n\n#### size 4'
        } else if (size === 5) {
          this.users.bio = this.users.bio + '\n\n##### size 5'
        }
      },

      openTabs: function (event) {
        var tabId = event.currentTarget.id
        var i = 0
        var tabcontent = document.getElementsByClassName('tabcontent')
        var tablinks = document.getElementsByClassName('tabs_links')

        for (i = 0; i < tabcontent.length; i++) {
          tabcontent[i].style.display = 'none'
        }

        for (i = 0; i < tablinks.length; i++) {
          tablinks[i].className = tablinks[i].className = 'tabs_links'
        }

        document.getElementById('tab_' + tabId).style.display = 'block'
        event.currentTarget.children[0].className = 'tabs_links active'
      },

      previewThumbnail: function (event) {
        var input = event.target
        if (input.files && input.files[0]) {
          var reader = new FileReader()
          var vm = this

          reader.onload = function (e) {
            vm.upload.data = e.target.result
          }

          reader.readAsDataURL(input.files[0])
        }
      },

      form_upload_data: function (e) {
        e.preventDefault()
        this.loading_button = false
        this.$http.post(this.api_url + '/api/users/upload/photo', this.upload, {headers: {'Authorization': localStorage.getItem('token')}}).then(response => {
          response.json().then((data) => {
            if (data.status) {
              this.$toast.info({message: data.message, position: 'top right'})
              this.upload.data = false
              this.picture_load()
              this.auth_users()
            } else {
              this.$toast.error({message: data.message, position: 'top right'})
            }
          })
          this.loading_button = true
        })
      },

      submit_tags: function () {
        this.$http.put(this.api_url + '/api/users/tags', {'tags': this.users.tags}, {headers: {'Authorization': localStorage.getItem('token')}}).then(response => {
          response.json().then((data) => {
            if (data.status) {
              this.$toast.info({message: data.message, position: 'top right'})
            } else {
              this.$toast.error({message: data.message, position: 'top right'})
            }
          })
        })
      },

      picture_load: function () {
        this.$http.get(this.api_url + '/api/users/photo', {headers: {'Authorization': localStorage.getItem('token')}}).then(response => {
          response.json().then((data) => {
            if (data.status) {
              this.pictures = data.images
              if (data.images[0]) {
                this.userdata.picture = data.images[0].url
              }
            } else {
              if (data.message != null) {
                this.$toast.error({message: data.message, position: 'top right'})
              }
            }
          })
        })
      },

      setas_profile: function (piid) {
        this.$http.put(this.api_url + '/api/users/photo/set/profile', {'piid': piid}, {headers: {'Authorization': localStorage.getItem('token')}}).then(response => {
          response.json().then((data) => {
            if (data.status) {
              this.pictures = data.images
              if (data.images[0]) {
                this.userdata.picture = data.images[0].url
              }
              this.auth_users()
              this.$toast.info({message: data.message, position: 'top right'})
            } else {
              this.$toast.error({message: data.message, position: 'top right'})
            }
          })
        })
      },

      set_password: function (event) {
        event.preventDefault()
        this.$http.put(this.api_url + '/api/users/update/password', { 'old': this.security.old, 'password': this.security.password, 'newpassword': this.security.newpassword }, {headers: {'Authorization': localStorage.getItem('token')}}).then(response => {
          response.json().then((data) => {
            if (data.status) {
              this.$toast.info({message: data.message, position: 'top right'})
            } else {
              this.$toast.error({message: data.message, position: 'top right'})
            }
          })
        })
      },

      enabled_doubleauth: function (event) {
        event.preventDefault()
        this.$http.put(this.api_url + '/api/users/auth/enable/double', { 'secret': this.users.double_auth_d['key'], 'code': this.security.code }, {headers: {'Authorization': localStorage.getItem('token')}}).then(response => {
          response.json().then((data) => {
            if (data.status) {
              this.users.double_auth = true
              this.security.code = ''
              this.$toast.info({message: data.message, position: 'top right'})
            } else {
              this.$toast.error({message: data.message, position: 'top right'})
            }
          })
        })
      },

      disable_doubleauth: function (event) {
        event.preventDefault()
        this.$http.put(this.api_url + '/api/users/auth/disable/double', { 'code': this.security.code }, {headers: {'Authorization': localStorage.getItem('token')}}).then(response => {
          response.json().then((data) => {
            if (data.status) {
              this.users.double_auth = false
              this.security.code = ''
              this.$toast.info({message: data.message, position: 'top right'})
            } else {
              this.$toast.error({message: data.message, position: 'top right'})
            }
          })
        })
      },

      delete_image: function (piid) {
        this.$http.delete(this.api_url + '/api/users/photo/delete/profile', {
          body: {'piid': piid},
          headers: {'Authorization': localStorage.getItem('token')}
        }).then(response => {
          response.json().then((data) => {
            if (data.status) {
              this.pictures = data.images
              this.auth_users()
              if (data.images[0]) {
                this.userdata.picture = data.images[0].url
              }
              this.$toast.info({message: data.message, position: 'top right'})
            } else {
              this.$toast.error({message: data.message, position: 'top right'})
            }
          })
        })
      },

      get_tags: function () {
        axios.get(this.api_url + '/api/users/tags/list', {headers: {'Authorization': localStorage.getItem('token')}}).then(response => {
          if (response.data.status) {
            this.options = response.data.data
          }
        })
      },

      addTag (newTag) {
        this.options.push(newTag)
        this.users.tags.push(newTag)
      },

      profile_loaded: function () {
        if (this.users.title === null || this.users.bio === null) {
          this.$router.push('/')
        }
        this.picture_load()
      },

      submit_address: function (e) {
        this.buttonAnimation(e, false, 0)
        if (!this.users.set_location && !this.addressData || (!this.users.set_location && (!this.addressData.longitude || !this.addressData.latitude))) {
          this.$toast.error({message: 'Please enter a valid address', position: 'top right'})
          this.buttonAnimation(e, true, 'Saves <i class="material-icons right">done</i>')
        } else {
          var data = { 'set_location': this.users.set_location }
          if (!this.users.set_location) {
            data = {
              'longitude': this.addressData.longitude,
              'latitude': this.addressData.latitude,
              'set_location': this.users.set_location
            }
          } else {
            if (navigator.geolocation) {
              navigator.geolocation.getCurrentPosition((coords) => {
                axios.put(this.api_url + '/api/geocalisation/set', {
                  'longitude': coords.coords.longitude,
                  'latitude': coords.coords.latitude
                }, {headers: {'Authorization': localStorage.getItem('token')}})
              }, () => {
                axios.put(this.api_url + '/api/geocalisation/set/ip', {}, {headers: {'Authorization': localStorage.getItem('token')}})
              }, {enableHighAccuracy: true})
            } else {
              axios.put(this.api_url + '/api/geocalisation/set/ip', {}, {headers: {'Authorization': localStorage.getItem('token')}})
            }
          }
          this.$http.put(this.api_url + '/api/users/set/location', data, {headers: {'Authorization': localStorage.getItem('token')}}).then(response => {
            response.json().then((data) => {
              if (data.status) {
                this.$toast.info({message: data.message, position: 'top right'})
              } else {
                this.$toast.error({message: data.message, position: 'top right'})
              }
            })
          }).catch(response => {
            this.$router.push('/error')
          })
          this.buttonAnimation(e, true, 'Saves <i class="material-icons right">done</i>')
          this.auth_users()
        }
      },

      getAddressData: function (addressData, placeResultData) {
        this.addressData = addressData
      },

      buttonAnimation: function (e, status, message) {
        if (!status) {
          e.currentTarget.innerHTML = '<i class="fa fa-circle-o-notch fa-spin fa-3x fa-fw"></i>'
          e.currentTarget.className = 'btn waves-effect waves-light red accent-2 disabled'
        } else {
          e.currentTarget.innerHTML = message
          e.currentTarget.className = 'btn waves-effect waves-light red accent-2'
        }
      }

    },

    created () {
      config.check_session().then(response => {
        if (response.data.status === true) {
          this.users = response.data.data
          this.profile_loaded()
        } else {
          this.$router.push('/login')
        }
      }).catch(response => {
        this.$router.push('/login')
      })
    }

  }
</script>

<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>
<style>
    body {
        font-family: Helvetica, sans-serif;
    }

    .input-tag {
        display: inline-block;
        height: 32px !important;
        padding: 0 12px !important;
        border-radius: 16px !important;
        background-color: #e4e4e4 !important;
        margin-bottom: 5px !important;
        margin-right: 5px !important;
        border: none !important;
    }

    .input-tag span {

        font-size: 13px !important;
        font-weight: 500 !important;
        color: rgba(0, 0, 0, 0.6) !important;
        line-height: 32px !important;
    }

    .input-tag .remove {
        color: grey !important;
    }

    .remove:hover {
        color: darkgray !important;
    }

    .vue-input-tag-wrapper {
        border: none !important;
    }

    .tab {
        cursor: pointer;
    }

    .biography_tabs {
        width: 350px;
        height: 40px;
        padding: 0;
        list-style-type: none;
        margin: 0;
        background: #f5f5f5;
    }

    .biography_tabs li
    {
        margin: 0;
        display: inline-block;
        width: auto;
        height: 40px;
        cursor: pointer;
        line-height: 45px;
        padding: 5px 15px;
    }

    .biography_tabs li:hover {
        background: #ff5252;
        color: #fff;
    }

    .biography_tabs ul
    {
        padding: 0;
        list-style: none;
        position: absolute;
        left: auto;
        margin: 0;
        display: none;
        z-index: 10;
        background-color: #ff5252;
        margin-top: -18px;
        width: 80px;
    }

    .biography_tabs ul li {
        height: 40px;
        line-height: 30px;
    }

    .biography_tabs li:hover ul
    {
        display: block;
    }


    .biography_tabs ul li:hover
    {
        background-color: #333;
    }

    strong {
        font-weight: bold;
    }
</style>



