<template>
  <div>
    <div class="background-image"></div>
    <div class="background-over"></div>
    <div class="container" style="margin-top: 100px">
      <div class="row">
        <div class="col s12 m12">
          <div class="card">
            <div class="card-content">
              <span class="card-title">OTP Code (Double Auth)</span>
              <form v-on:submit.prevent="codesubmit">
                <div class="row">
                  <div class="input-field inline col s12">
                    <input id="code" v-model="code" type="number" autofocus>
                    <label for="code">Code</label>
                  </div>
                </div>
                <div class="right-align">
                  <button class="btn waves-effect waves-light red accent-2" type="submit" name="action">Unlock session
                    <i class="material-icons right">lock_open</i>
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>


<script type="text/babel">

  import config from './Config.vue'

  export default {
    name: 'Otp',
    data () {
      return {
        code: ''
      }
    },

    methods: {
      codesubmit: function (e) {
        e.preventDefault()
        if (this.code === '') {
          this.$toast.error({ message: 'OTP Code is required' })
        } else {
          this.$http.post(config.url_api + '/api/users/auth/unlock', { 'code': this.code, 'session_token': localStorage.getItem('token') }).then(response => {
            response.json().then((data) => {
              if (data.status === true) {
                this.$toast.info({message: data.message})
                this.$router.push('/')
              } else {
                this.$toast.error({message: data.message})
              }
            })
          }, response => {
            this.$router.push('/error')
            this.$toast.error({message: 'Connection error with server'})
          })
        }
      }
    },

    beforeCreate () {
      config.check_session().then(response => {
        if (response.data.status === true) {
          this.$router.push('/')
        }
      }).catch(response => {
        this.$router.push('/error')
        this.$toast.error({message: 'Connection error with server'})
      })
    }
  }

</script>

<style media="screen">
  body
  {
    animation: cdhange-color 8s infinite;
  }
</style>



