import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import router from '../router'

Vue.use(Vuex)
export default new Vuex.Store({
  state: {
    api_url: 'http://' + window.location.hostname + ':8888',
    api_photo: 'http://' + window.location.hostname + ':8084',
    connect: false,
    clientId: false,
    userdata: {},
    list_friends: {},
    loading_modal: true,
    loading_friends: true,
    data_chat: {
      users: {},
      messages: {}
    },
    data_chat_id: 0,
    message: null,
    notifications: {
      new: false
    }
  },
  mutations: {
    SOCKET_CONNECT: (state, status) => {
      console.log('WebSocket: server responding OK')
    },

    SOCKET_DISCONNECT: (state, status) => {
      if (state.connect === true) {
        (new Vue()).$toast.error({message: 'Waiting for server ...', position: 'bottom left', timeOut: 8000})
      }
      if (state.connect) {
        state.connect = false
        axios.delete(state.api_url + '/api/messageries/destroy', {
          data: {'socket_id': state.clientId},
          headers: {'Authorization': localStorage.getItem('token')}
        })
      }

      console.log('WebSocket: server/client disconnected')
    },

    SOCKET_MESSAGE: (state, message) => {
      (new Vue()).$toast.info({message: message, position: 'bottom left', timeOut: 8000})
    },

    SOCKET_AUTHORIZATION_RESPONSE: (state, message) => {
      state.connect = message.status
      state.clientId = message.clientId
    },

    SOCKET_MESSAGERIE_ERROR: (state, body) => {
      alert('Error to send message: ' + body.message)
      router.push('/messaging')
    },

    SOCKET_MESSAGERIES: (state, body) => {
      if (body.type === 'friends_list') {
        state.list_friends = body.data
        state.loading_friends = false
      } else if (body.type === 'friend_connected') {
        if (state.list_friends[body.uid]) {
          state.list_friends[body.uid]['connected'] = true
        }
      } else if (body.type === 'friend_disconnected') {
        state.list_friends[body.uid]['connected'] = false
      } else if (body.type === 'new_message') {
        if (body.data.uid_sender !== state.userdata.uid) {
          if (body.count_message !== -1) {
            state.list_friends[body.data.uid_sender]['count_message'] = body.count_message
          }
          state.list_friends[body.data.uid_sender]['message'] = body.data.message
        } else {
          if (body.count_message !== -1) {
            state.list_friends[body.data.uid_receiver]['count_message'] = body.count_message
          }
          state.list_friends[body.data.uid_receiver]['message'] = body.data.message
        }
        if (parseInt(state.data_chat_id) === parseInt(body.data.uid_receiver) || parseInt(state.data_chat_id) === parseInt(body.data.uid_sender)) {
          state.data_chat.messages.push(body.data)
        }
      } else if (body.type === 'viewed_message') {
        if (state.list_friends[body.uid]) {
          state.list_friends[body.uid]['count_message'] = body.count_message
        }
      }
    },

    SOCKET_NOTIFICATIONS: (state, message) => {
      if (message.type === 'list') {
        state.notifications.data = message.data
        state.notifications.new = message.new
      } else if (message.type === 'new') {
        (new Vue()).$toast.success({message: 'New : ' + message.data.title, position: 'bottom left', timeOut: 8000})
        state.notifications.data.unshift(message.data)
        state.notifications.new = true
      }
    },

    SOCKET_MESSAGERIE_CONNECTED: (state, message) => {
      state.list_friends[30].connected = true
    },

    AUTH_USERS: (state, status) => {
      axios.get(state.api_url + '/api/session', {headers: {'Authorization': localStorage.getItem('token')}}).then(response => {
        if (response.data.status === true) {
          state.userdata = response.data.data
          state.loading_modal = false
        } else if (response.data.action === 'otp_required') {
          router.push('/otp')
        } else {
          router.push('/login')
        }
      }).catch(function () {
        router.push('/error')
      })
    },

    LOGOUT: (state, status) => {
      let vue = new Vue()
      vue.$toast.info({message: 'You are now logged out', position: 'top right', timeOut: 8000})
      state.connect = false
      state.clientId = false
      state.list_friends = false
      state.userdata = false
      vue.$socket.disconnect()
      router.push('/login')
      localStorage.removeItem('token')
    }
  },

  getters: {
    connect: state => state.connect,
    clientId: state => state.clientId,
    userdata: state => state.userdata,
    loading_modal: state => state.loading_modal,
    loading_friends: state => state.loading_friends,
    list_friends: state => state.list_friends,
    data_chat: state => state.data_chat,
    data_chat_id: state => state.data_chat_id,
    notifications: state => state.notifications,
    api_url: state => state.api_url,
    api_photo: state => state.api_photo
  },

  actions: {
    socket_auth: (context, type) => {
      let vue = (new Vue())
      if (!localStorage.getItem('token')) {
        vue.$socket.disconnect()
        return false
      } else {
        vue.$socket.connect()
        vue.$socket.emit('authorization', {token: localStorage.getItem('token')})
        return true
      }
    },
    auth_users: (context, type) => {
      context.commit('AUTH_USERS', type)
      return true
    },
    logout: (context, type) => {
      context.commit('LOGOUT', type)
    }
  }
})



