import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Login from '@/components/Login'
import Register from '@/components/Register'
import Settings from '@/components/Settings'
import Forgot from '@/components/Forgot'
import Messagerie from '@/components/Messagerie'
import Messageriechat from '@/components/Messagerie_chat.vue'
import Error from '@/components/Error.vue'
import Profile from '@/components/Profile.vue'
import Reset from '@/components/Reset.vue'
import Otp from '@/components/Otp.vue'
import Notifications from '@/components/Notifications.vue'
import ProfileViewed from '@/components/ProfileViewed.vue'
import ProfileInterested from '@/components/ProfileInterested.vue'
import SideBar from '@/components/Menu.vue'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '',
      component: SideBar,
      children: [
        {
          path: '/',
          name: 'Home',
          component: Home,
          meta: {'keepAlive': false}
        },
        {
          path: '/search/:tags',
          name: 'Tags',
          component: Home,
          meta: {'keepAlive': false}
        },
        {
          path: 'settings',
          name: 'Settings',
          component: Settings,
          meta: {'keepAlive': true}
        },
        {
          path: '/messaging',
          name: 'Messagerie',
          component: Messagerie,
          meta: {'keepAlive': false}
        },
        {
          path: '/messageriechat/:id',
          name: 'messageriechat',
          component: Messageriechat,
          meta: {'keepAlive': false}
        },
        {
          path: '/profile/:username',
          name: 'Profile',
          component: Profile,
          meta: {'keepAlive': false}
        },
        {
          path: '/notifications',
          name: 'Notifications',
          component: Notifications,
          meta: {'keepAlive': false}
        },
        {
          path: '/me/viewed',
          name: 'ProfileViewed',
          component: ProfileViewed,
          meta: {'keepAlive': false}
        },
        {
          path: '/me/interested',
          name: 'ProfileInterested',
          component: ProfileInterested,
          meta: {'keepAlive': false}
        }]
    },
    {
      path: '/reset/:token',
      name: 'Reset',
      component: Reset,
      meta: {'keepAlive': false}
    },
    {
      path: '/error',
      name: 'error',
      component: Error,
      meta: {'keepAlive': false}
    },
    {
      path: '/otp',
      name: 'Otp',
      component: Otp,
      meta: {'keepAlive': false}
    },
    {
      path: '/login',
      name: 'Login',
      component: Login,
      meta: {'keepAlive': false}
    },
    {
      path: '/register',
      name: 'Register',
      component: Register,
      meta: {'keepAlive': false}
    },
    {
      path: '/forgot',
      name: 'Forgot',
      component: Forgot,
      meta: {'keepAlive': false}
    }
  ]
})



