<template>
    <div class="">

        <div class="bloc">
            <div class="message_loading" v-if="!profile_data.status">
                <div class="preloader-wrapper big active">
                    <div class="spinner-layer spinner-red-only">
                        <div class="circle-clipper left"><div class="circle"></div></div><div class="gap-patch"><div class="circle"></div></div>
                        <div class="circle-clipper right"><div class="circle"></div></div>
                    </div>
                </div>
            </div>
            <div id="profile-page">
                <div id="profile-page-header" class="card profile_card no-shadow">
                    <div class="card-image waves-effect waves-block waves-light blue-grey darken-4">
                        <div class="profile_background activator" style="background-image: url(https://upload.wikimedia.org/wikipedia/commons/9/97/Pont_des_Arts%2C_Paris.jpg)" alt="user background"></div>
                        <div class="profile_circle activator z-depth-3 blue-grey darken-3" :style="{ 'background-image': 'url(' + profile_data.imageBase64 + ')' }"></div>
                        <div class="profile_data hide-on-small-only center-align" v-if="profile_data.data.uid != userdata.uid">

                            <a v-on:click="chat()" v-bind:class="{'disabled': !profile_data.picturesHave || !userdata.pictureHave}" class="waves-effect waves-light btn"><i class="material-icons right">message</i> chat</a>
                            <a v-if="!profile_data.uid_likes" v-on:click="profileLike();" v-bind:class="{'disabled': !profile_data.picturesHave || !userdata.pictureHave}" class="waves-effect waves-light btn red accent-2"><i class="material-icons right">favorite</i> interested</a>
                            <a v-if="profile_data.uid_likes" v-on:click="profileLike();" v-bind:class="{'disabled': !profile_data.picturesHave || !userdata.pictureHave}" class="waves-effect waves-light btn red accent-2"><i class="material-icons right">favorite_border</i> not interested</a>
                            <a v-on:click="profileReport()" class="waves-effect waves-light btn indigo darken-3"><i class="material-icons right">report</i> Report</a>
                            <a v-on:click="profileBlock()" class="waves-effect waves-light btn blue-grey darken-4"><i class="material-icons right">block</i> Block</a>

                        </div>

                      <div class="profile_data hide-on-small-only center-align" v-if="profile_data.data.uid == userdata.uid">

                        <router-link to="/me/interested" class="waves-effect waves-light btn red accent-2"><i class="material-icons right">favorite</i> they are interested</router-link>
                        <router-link to="/me/viewed" class="waves-effect waves-light btn indigo darken-3"><i class="material-icons right">remove_red_eye</i> they saw my profile</router-link>
                        <router-link to="/settings" class="waves-effect waves-light btn blue-grey darken-4"><i class="material-icons right">settings</i> Manage</router-link>

                      </div>
                    </div>
                    <div class="card-content profile_card-content" style="height: 100px;">
                        <div class="row" style="margin-top: -20px;">
                            <div class="col s4 m6 l8 offset-s0">
                                <h4 class="card-title profile_card-title grey-text text-darken-4">
                                    {{ profile_data.data.username }}
                                    <i v-if="profile_data.data.connected !== '0'" class="fa fa-circle small_icone green-text"></i>
                                    <i v-if="profile_data.data.sexe === '0'" class="fa fa-transgender small_icone light-green-text text-accent-4 pointer" v-my-tooltip.bottom-center="'Other sex'" aria-hidden="true"></i>
                                    <i v-if="profile_data.data.sexe === '1'" class="fa fa-mars small_icone light-blue-text pointer" aria-hidden="true" v-my-tooltip.bottom-center="'Man'"></i>
                                    <i v-if="profile_data.data.sexe === '2'" class="fa fa-venus small_icone pink-text lighten-1 pointer" aria-hidden="true" v-my-tooltip.bottom-center="'Woman'"></i>
                                    <i v-if="profile_data.you_interested && profile_data.uid_likes && profile_data.data.uid != userdata.uid" class="fa fa-check-circle small_icone pointer green-text" v-my-tooltip.bottom-center="'You matche'" aria-hidden="true"></i>
                                </h4>
                                <p class="medium-small grey-text truncate hide-on-small-only">{{ profile_data.data.title }} </p>
                            </div>
                            <div class="col s4 m3 l2 center-align">
                                <h4 class="card-title profile_card-title grey-text text-darken-4">
                                    <i class="material-icons green-text" v-if="profile_data.popularity >= 8" v-my-tooltip.bottom-center="'Very popular : ' + profile_data.popularity">sentiment_very_satisfied</i>
                                    <i class="material-icons blue-text" v-else-if="profile_data.popularity >= 5" v-my-tooltip.bottom-center="'Popular : ' + profile_data.popularity">sentiment_satisfied</i>
                                    <i class="material-icons orange-text" v-else-if="profile_data.popularity >= 3 " v-my-tooltip.bottom-center="'Score : ' + profile_data.popularity">sentiment_neutral</i>
                                    <i class="material-icons  orange-text text-darken-1" v-else v-my-tooltip.bottom-center="'Score : ' +  profile_data.popularity">sentiment_very_dissatisfied</i>
                                </h4>
                                <p class="medium-small grey-text truncate">popularity</p>
                            </div>
                            <div class="col s4 m3 l2 center-align">
                                <h4 class="card-title profile_card-title grey-text text-darken-4">{{ profile_data.likes }}</h4>
                                <p class="medium-small grey-text truncate">people interested</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
                <div v-if="!profile_data.picturesHave || !userdata.pictureHave" class="alertPicture">
                  You or this user must have profile picture to interact
                </div>
                <div class="nav_profile hide-on-med-and-up">
                        <a v-on:click="chat()" v-bind:class="{'disabled': !profile_data.picturesHave || !userdata.pictureHave}" class="waves-effect waves-light btn"><i class="material-icons right">message</i> chat</a>
                        <a v-if="!profile_data.uid_likes" v-on:click="profileLike();" v-bind:class="{'disabled': !profile_data.picturesHave || !userdata.pictureHave}" class="waves-effect waves-light btn pink darken-3"><i class="material-icons right">favorite</i> interested</a>
                        <a v-if="profile_data.uid_likes" v-on:click="profileLike();" v-bind:class="{'disabled': !profile_data.picturesHave || !userdata.pictureHave}" class="waves-effect waves-light btn pink darken-3"><i class="material-icons right">favorite_border</i> not interested</a>
                        <a v-on:click="profileReport();" class="waves-effect waves-light btn indigo darken-3"><i class="material-icons right">report</i> Report</a>
                        <a v-on:click="profileBlock();"class="waves-effect waves-light btn blue-grey darken-4"><i class="material-icons right">block</i> Block</a>
                </div>

            <div class="row">
                <div class="col s12 m12 l6">
                <div class="col s12" style="margin-top:10px;">
                    <div class="card no-shadow">
                        <div class="card-content">
                            <span class="card-title">Biography</span>
                            <p v-html="compiledMarkdown">{{ profile_data.data.bio }}</p>
                        </div>
                        <div class="card-action">
                          <div v-for="item in profile_data.tags" class="chip" >
                            <router-link :to="{ name: 'Tags', params: { tags: item }}" class="black-text"> #{{ item }} </router-link>
                          </div>
                        </div>
                    </div>
                </div>
                </div>
                <div class="col s12 m12 l6">
                <div class="col s12" style="margin-top:10px;">
                    <div class="card no-shadow">
                        <div class="card-content">
                            <table class="striped">
                                <tbody>
                                <tr>
                                    <td>Age <i class="material-icons left">cake</i></td>
                                    <td class="right-align" v-if="profile_data.data.birthday !== 'Not specified'">{{ profile_data.data.birthday }} years</td>
                                    <td class="right-align" v-else> <p class="medium-small grey-text">{{ profile_data.data.birthday }}</p></td>
                                </tr>
                                <tr>
                                    <td>I am interested in <i class="material-icons left">whatshot</i></td>
                                    <td class="right-align" v-if="profile_data.data.orientation === '0' && profile_data.data.sexe !== '0'"> man & woman</td>
                                    <td class="right-align" v-if="profile_data.data.orientation === '2' && profile_data.data.sexe === '1'"> man</td>
                                    <td class="right-align" v-if="profile_data.data.orientation === '2' && profile_data.data.sexe === '2'"> woman</td>
                                    <td class="right-align" v-if="profile_data.data.orientation === '1' && profile_data.data.sexe === '1'"> woman</td>
                                    <td class="right-align" v-if="profile_data.data.orientation === '1' && profile_data.data.sexe === '2'"> man</td>

                                    <td class="right-align" v-if="profile_data.data.orientation === '1' && profile_data.data.sexe === '0'"> man & woman</td>
                                    <td class="right-align" v-if="profile_data.data.orientation === '2' && profile_data.data.sexe === '0'"> others</td>
                                    <td class="right-align" v-if="profile_data.data.orientation === '0' && profile_data.data.sexe === '0'"> man, woman & others</td>
                                </tr>
                                <tr>
                                    <td>Looking for <i class="material-icons left">pageview</i></td>
                                    <td class="right-align" v-if="profile_data.data.lookingfor !== 'Not specified'">{{ profile_data.data.lookingfor }}</td>
                                    <td class="right-align" v-else><p class="medium-small grey-text"> {{ profile_data.data.lookingfor }} </p></td>
                                </tr>
                                <tr>
                                  <td>Distance <i class="material-icons left">gps_fixed</i></td>
                                  <td class="right-align">{{ profile_data.data.distance | formatdistance }}</td>
                                </tr>
                                <tr>
                                    <td>Last connection <i class="material-icons left">access_time</i></td>
                                    <td v-if="profile_data.data.connected !== '0'" class="right-align green-text"><strong>currently connected</strong></td>
                                    <td v-else class="right-align">{{ profile_data.data.last_connection | formatdate }}</td>
                                </tr>
                                </tbody>
                            </table>

                        </div>
                    </div>
                </div>

                <div class="col s12" style="margin-top:10px;">
                    <div class="card no-shadow">
                        <div class="card-content">
                            <span class="card-title">Pictures</span>
                            <p v-if="!profile_data.pictures">No picture</p>
                            <img class="profile_pictures" v-for="item in profile_data.pictures" :src="item.pictures" alt="item.comment" v-my-tooltip.bottom-center="item.short_comment">

                        </div>
                    </div>
                </div>
                </div>
            </div>
        </div>


        <simplert :useRadius="true"
                  :useIcon="true"
                  ref="simplert">
        </simplert>
    </div>
</template>

<script type="text/babel">
  import Socket from './Socket.js'
  import Vuex from 'vuex'
  import axios from 'axios'
  import Simplert from 'vue2-simplert'

  let marked = require('marked')
  export default {
    store: Socket,
    name: 'Profile',
    data () {
      return {
        users: {},
        profile_data: {
          status: false,
          data: {},
          tags: {}
        },
        welcome_modal: false
      }
    },

    components: {Simplert},

    computed: {
      ...Vuex.mapGetters(['clientId', 'connect', 'userdata', 'list_friends', 'loading_friends', 'loading_modal', 'api_url']),

      compiledMarkdown: function () {
        return marked(this.profile_data.data.bio || '')
      }
    },

    methods: {
      ...Vuex.mapActions([]),

      get_data: function () {
        axios.get(this.api_url + '/api/profile/' + this.$route.params.username, {headers: {'Authorization': localStorage.getItem('token')}}).then(response => {
          if (response.data.status === false) {
            this.$toast.error({message: response.data.message, position: 'top right'})
            this.$router.push('/')
          } else {
            this.profile_data = response.data
            if (this.profile_data.data.uid !== this.profile_data.viewer_profile.uid) {
              this.$socket.emit('notifications_new', {
                'uid': this.profile_data.data.uid,
                'title': 'A new guy has seen your profile',
                'message': this.profile_data.viewer_profile.username + ' has visited your profile',
                timestamp: 25,
                uid_interested: this.profile_data.viewer_profile.uid
              })
            }
          }
        }).catch(function () {
          console.log('Error get profile')
        })
      },

      chat: function () {
        this.profile_data.status = false
        axios.get(this.api_url + '/api/messageries/chat/' + this.profile_data.data.uid, { headers: { 'Authorization': localStorage.getItem('token') } }).then(response => {
          if (!response.data.status) {
            this.$toast.error({message: response.data.message, position: 'bottom left'})
          } else {
            this.$socket.emit('get_friends', {})
            this.$socket.emit('get_friends_uid', this.profile_data.data)
            this.$router.push('/messageriechat/' + this.profile_data.data.uid)
          }
          this.profile_data.status = true
        })
      },

      profileLike: function () {
        axios.put(this.api_url + '/api/profile/' + this.$route.params.username + '/likes', {}, {headers: {'Authorization': localStorage.getItem('token')}}).then(response => {
          if (response.data.status === false) {
            this.$toast.error({message: response.data.message, position: 'bottom left'})
          } else {
            if (response.data.uid_likes) {
              this.$socket.emit('notifications_new', {
                'uid': this.profile_data.data.uid,
                'title': 'Someone is interested in you',
                'message': this.userdata.username + ' is interested for you',
                timestamp: 25,
                uid_interested: this.userdata.uid
              })
              if (this.profile_data.you_interested === response.data.uid_likes) {
                this.$socket.emit('notifications_new', {
                  'uid': this.profile_data.data.uid,
                  'type': 'match',
                  'title': 'You have a new match',
                  'message': this.userdata.username + ' has matched with you',
                  timestamp: 25,
                  uid_interested: this.userdata.uid
                })
                this.$socket.emit('notifications_new', {
                  'uid': this.userdata.uid,
                  'title': 'You have a new match',
                  'type': 'match',
                  'message': this.profile_data.data.username + ' has matched with you',
                  timestamp: 25,
                  uid_interested: this.profile_data.data.uid
                })
              }
            } else {
              this.$socket.emit('notifications_new', {
                'uid': this.profile_data.data.uid,
                'title': 'A person is no longer interested',
                'message': this.userdata.username + ' is no longer interested',
                timestamp: 25,
                uid_interested: this.userdata.uid
              })
            }
            this.profile_data.likes = response.data.likes
            this.profile_data.uid_likes = response.data.uid_likes
            this.$toast.info({message: response.data.message, position: 'bottom left'})
          }
        }).catch(function () {
          this.$router.push('/error')
        })
      },

      profileBlockAction: function () {
        axios.post(this.api_url + '/api/profile/' + this.$route.params.username + '/block', {}, {headers: {'Authorization': localStorage.getItem('token')}}).then(response => {
          console.log(response.data)
          if (response.data.status === false) {
            this.$toast.error({message: response.data.message, position: 'top right'})
          } else {
            this.$socket.emit('get_friends', {})
            this.$toast.info({message: response.data.message, position: 'top right'})
            this.$router.push('/')
          }
        })
      },

      profileReportAction: function () {
        axios.post(this.api_url + '/api/profile/' + this.$route.params.username + '/report', {}, {headers: {'Authorization': localStorage.getItem('token')}}).then(response => {
          console.log(response.data)
          if (response.data.status === false) {
            this.$toast.error({message: response.data.message, position: 'top right'})
          } else {
            this.$toast.info({message: response.data.message, position: 'top right'})
            let obj = {
              title: 'Block this user ?',
              message: 'Do you want to block this user ?',
              type: 'error',
              useConfirmBtn: true,
              customConfirmBtnClass: 'waves-effect waves-light btn blue-grey darken-4 btn-marge',
              customCloseBtnClass: 'waves-effect waves-light btn red accent-2 btn-marge',
              onConfirm: this.profileBlock
            }
            this.$refs.simplert.openSimplert(obj)
          }
        })
      },

      profileBlock: function () {
        let obj = {
          title: 'Are you sure?',
          message: 'By blocking this user you will prevent him from contacting you, and you would delete your common data.',
          type: 'error',
          useConfirmBtn: true,
          customConfirmBtnClass: 'waves-effect waves-light btn blue-grey darken-4 btn-marge',
          customCloseBtnClass: 'waves-effect waves-light btn red accent-2 btn-marge',
          onConfirm: this.profileBlockAction
        }
        this.$refs.simplert.openSimplert(obj)
      },

      profileReport: function () {
        let obj = {
          title: 'Report user',
          message: 'Report this account as <span class="red-text">fake</span> or as <span class="red-text">inappropriate</span>',
          type: 'warning',
          useConfirmBtn: true,
          customConfirmBtnClass: 'waves-effect waves-light btn blue-grey darken-4 btn-marge',
          customCloseBtnClass: 'waves-effect waves-light btn red accent-2 btn-marge',
          onConfirm: this.profileReportAction
        }
        this.$refs.simplert.openSimplert(obj)
      },

      profile_loaded: function () {
        if (this.userdata.title === null || this.userdata.bio === null) {
          this.welcome_modal = true
        }
      }
    },

    created () {
      this.get_data()
    }
  }
</script>


<style media="screen">
    .profile_background {
        width: 100%;
        height: 250px;
        background-size: cover;
        background-position: center;
    }

    .profile_data {
        position: absolute;
        width: 350px;
        height: 220px;
        right: 20px;
        top: 0;
        bottom: 0;
        margin: auto;
        padding: 25px;
        background: rgba(0, 0, 0, 0.6);
    }

    .profile_data .btn {
        width: 100%;
        text-align: left;
        margin-bottom: 10px;
    }

    .profile_online {
        width: 13px;
        height: 13px;
        background: green;
        display: inline-block;
        border-radius: 100%;
    }

    .profile_pictures {
        max-height: 150px;
        max-width: 100%;
        border: 2px solid #e0e0e0;
        border-radius: 2px;
        margin: 10px;
        cursor: pointer;
    }

    .profile_pictures:hover {
        border: 2px solid #bdbdbd;
    }

    .nav_profile {
        width: 100%;
        height: 45px;
        position: relative;
        float: left;
        background: red;
        margin: 0;
        padding: 0;
    }

    .nav_profile .btn {
        margin: 0;
        position: relative;
        float: left;
        width: 25%;
        height: 45px;
        line-height: 45px;
        border-radius: 0;
        display: inline-block;
    }

    .profile_card {
        margin: 0 !important;
    }

    .profile_circle {
        width: 180px;
        height: 180px;
        border: 2px solid white;
        border-radius: 4px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        border-radius: 100%;
        position: absolute;
        top: 0;
        left: 20px;
        bottom: 0;
        margin: auto;
    }

    .profile_card-content .profile_card-title {
        margin-bottom:0
    }


    td {
        padding-left: 25px;
        padding-right: 25px;
    }

    .btn-marge {
        margin: 5px;
    }

    .simplert__content--radius {
        border-radius: 0;
    }

    strong {
        font-weight: bold;
    }

    .small_icone {
      font-size: 20px;
    }

    .alertPicture {
      width: 100%;
      padding: 20px 20px;
      text-align: center;
      background: #DB0B32;
      color: white;
    }
</style>



