<template>
   <div>
       <nav class="blue-grey darken-4 hide-on-large-only">
           <div class="nav-wrapper" v-if="userdata">
               <ul class="nav-mobile center-align">
                   <li><router-link to="/notifications">
                    <i v-if="!notifications.new" class="material-icons white-text">notifications_none</i>
                    <i v-else class="material-icons white-text animated jello infinite">notifications_active</i>
                   </router-link></li>
                   <li><router-link v-if="userdata.username" :to="{ name: 'Profile', params: { username: userdata.username }}"><i class="material-icons">account_circle</i></router-link></li>
                   <li><router-link to="/"><i class="material-icons">gps_fixed</i></router-link></li>
                   <li><router-link to="/messaging"><i class="material-icons">forum</i></router-link></li>
                   <li><router-link to="/settings"><i class="material-icons">settings_applications</i></router-link></li>
                   <li><a v-on:click="logout"><i class="material-icons">input</i></a></li>
               </ul>
           </div>
       </nav>
       <ul id="slide-out" class="side-nav fixed" v-if="userdata">
           <li><div class="user-view">
               <div class="background blue-grey darken-4">
               </div>
               <a><div v-if="userdata.picture" class="img_cirlce" :style="{ 'background-image': 'url(' + userdata.picture + ')' }"></div></a>
               <a><span class="white-text name">{{ userdata.firstname }} {{ userdata.lastname }}</span></a>
               <a><span class="white-text email" style="font-size: 12px; font-style: italic;">{{ userdata.title }}</span></a>
             <div class="side_notif">
               <router-link to="/notifications">
                 <i v-if="!notifications.new" class="material-icons white-text">notifications_none</i>
                 <i v-else class="material-icons white-text animated jello infinite">notifications_active</i>
               </router-link>
             </div>
           </div>
           </li>
           <div class="collection" style="margin-top: -10px;">
                <router-link to="/" class="collection-item" v-bind:class="{ 'active': this.$route.name === 'Home' }">Finder</router-link>
                <router-link v-if="userdata.username" :to="{ name: 'Profile', params: { username: userdata.username }}" v-bind:class="{ 'active': this.$route.name === 'Profile' }" class="collection-item">Profile</router-link>
                <router-link to="/messaging" class="collection-item" v-bind:class="{ 'active': this.$route.name === 'Messagerie' || this.$route.name === 'messageriechat' }">Messsaging</router-link>
                 <router-link to="/settings" class="collection-item" v-bind:class="{ 'active': this.$route.name === 'Settings' }">Settings</router-link>
           </div>


           <ul class="collection" style="overflow: auto; height: 50%">
               <div v-for="item in list_friends">
                   <router-link class="collection-item avatar" :to="{ name: 'messageriechat', params: { id: item.uid_friend }}">
                       <span class="connected" v-if="item.connected"></span>
                       <img v-if="item.piid" :src="( api_url + '/api/users/photo/' + item.piid)" alt="" class="circle">
                       <img v-else :src="( api_url + '/api/users/photo/picture')" alt="" class="circle">
                       <span class="title">{{ item.username }} <span v-if="item.count_message !== 0" class="new badge"></span></span>
                       <p class="grey-text text-darken-2">{{ item.message | truncate(25, '...') }}</p>
                   </router-link>
               </div>
               <div v-if="loading_friends === true" class="center-align">
                   <div class="preloader-wrapper small active">
                       <div class="spinner-layer spinner-red-only">
                           <div class="circle-clipper left"><div class="circle"></div></div><div class="gap-patch"><div class="circle"></div></div><div class="circle-clipper right"><div class="circle"></div></div>
                       </div>
                   </div>
               </div>
           </ul>

           <button v-on:click="logout" class="btn waves-effect waves-light red accent-2 logout">Logout</button>

       </ul>

     <transition name="custom-classes-transition" enter-active-class="animated fadeIn">
       <keep-alive>
         <router-view v-if="$route.meta.keepAlive"></router-view>
       </keep-alive>
     </transition>

     <transition name="custom-classes-transition" enter-active-class="animated fadeIn">
       <router-view v-if="!$route.meta.keepAlive"></router-view>
     </transition>

   </div>

</template>



<script type="text/babel">

  import Vuex from 'vuex'
  import Socket from './Socket.js'
  import axios from 'axios'

  export default {
    store: Socket,
    name: 'SideBar',
    data () {
      return {
      }
    },

    computed: {
      ...Vuex.mapGetters(['clientId', 'connect', 'userdata', 'list_friends', 'loading_friends', 'loading_modal', 'notifications', 'api_url'])
    },

    methods: {
      ...Vuex.mapActions(['auth_users', 'logout', 'socket_auth'])
    },

    created () {
      console.log(this.api_url)
      axios.get(this.api_url + '/api/session', {headers: {'Authorization': localStorage.getItem('token')}}).then(response => {
        if (response.data.status === true) {
          Socket.state.userdata = response.data.data
          Socket.state.loading_modal = false
          if (Socket.state.userdata.set_location) {
            if (navigator.geolocation) {
              navigator.geolocation.getCurrentPosition((coords) => {
                axios.put(this.api_url + '/api/geocalisation/set', {
                  'longitude': coords.coords.longitude,
                  'latitude': coords.coords.latitude
                }, {headers: {'Authorization': localStorage.getItem('token')}})
              }, () => {
                axios.put(this.api_url + '/api/geocalisation/set/ip', {}, {headers: {'Authorization': localStorage.getItem('token')}})
              }, {enableHighAccuracy: true})
            } else {
              axios.put(this.api_url + '/api/geocalisation/set/ip', {}, {headers: {'Authorization': localStorage.getItem('token')}})
            }
          }
        } else if (response.data.action === 'otp_required') {
          this.$router.push('/otp')
        } else {
          this.$router.push('/login')
        }
      }).catch(() => {
        this.$router.push('/error')
      })
    }
  }
</script>

<style>
  .side_notif {
    position: absolute;
    height: auto;
    width: auto;
    bottom: 10px;
    right: 10px;
    z-index: 3;
  }
  #notif {
    animation-delay: 20s;
    animation-iteration-count: infinite;
  }

  .macth_extra {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 999999;
    margin: 0;
  }

  .macth_extra .title {
    margin-top: 5%;
  }

  .macth_extra .pictures div {
    width: 250px;
    height: 250px;
    border-radius: 100%;
    border: 5px solid #ff5252;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  }

</style>



