<template>

</template>

<script type="text/babel">
    import axios from 'axios'
    import Socket from './Socket.js'
    import Vuex from 'vuex'

    export default {
      store: Socket,
      name: 'Config',
      url_api: 'http://' + window.location.hostname + ':8888',
      check_session () {
        console.log(this.url_api)
        return axios.get(this.url_api + '/api/session', {headers: {'Authorization': localStorage.getItem('token')}})
      },

      computed: {
        ...Vuex.mapGetters(['api_url'])
      }
    }
</script>



