
import Vue from 'vue'
import App from './App'
import router from './router'
import VueResource from 'vue-resource'
import CxltToastr from 'cxlt-vue2-toastr'
import 'cxlt-vue2-toastr/dist/css/cxlt-vue2-toastr.css'
import Vue2Filters from 'vue2-filters'
import socketio from 'socket.io-client'
import VueSocketIO from 'vue-socket.io'
import Socket from './components/Socket'
import {Picker, Emoji} from 'emoji-mart-vue'
import VueSweetAlert from 'vue-sweetalert'
import { VTooltip } from 'v-tooltip'
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  localStorage: {},
  components: { App }
})

Vue.use(VueResource)

Vue.directive('my-tooltip', VTooltip)
Vue.filter('formatdate', function (value) {
  var date = new Date()
  var timestamp = new Date(value * 1000)
  var secondsPast = (date.getTime() - timestamp.getTime()) / 1000
  var tmp = timestamp + ''
  var match = tmp.match(/([a-zA-Z]{3}) ([a-zA-Z]{3}) ([0-9]{2}) ([0-9]{4}) ([0-9]{2}):([0-9]{2}):([0-9]{2})/)

  if (secondsPast < 60) {
    return 'a few seconds ago'
  } else if (secondsPast < 3600) {
    return parseInt(secondsPast / 60) + ' minutes ago'
  } else if (secondsPast <= 86400) {
    return parseInt(secondsPast / 3600) + ' hours ago'
  } else {
    return match[1] + ' ' + match[3] + ' ' + match[2] + ' ' + match[4] + ' at ' + match[5] + ':' + match[6]
  }
})

Vue.filter('formatdistance', function (value) {
  if (parseInt(value) === 0 || parseInt(value) <= 3) {
    return 'Near '
  } else {
    return Math.round(value) + ' km'
  }
})

Vue.filter('truncate', filter)
Vue.use(CxltToastr, { position: 'top right', closeButton: false })
Vue.use(Vue2Filters)
Vue.use(VueSweetAlert)
Vue.use(VueSocketIO, socketio('http://' + window.location.hostname + ':8082'), Socket)

var filter = function (text, length, clamp) {
  clamp = clamp || '...'
  var node = document.createElement('div')
  node.innerHTML = text
  var content = node.textContent
  return content.length > length ? content.slice(0, length) + clamp : content
}

Vue.component('picker', Picker)
Vue.component('emoji', Emoji)




